.hamburger-container {
  cursor: pointer;
  padding: 1vmin 1.5vmin 0 0;
  width: 6vmin;
  height: 6vmin;
  @media screen and(min-width:992px) {
    display: none;
  }
}

.hamburger-icon {
  animation: fade 0.7s ease-in-out;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
